  src:url("fonts/Genericons-Neue.eot?3d5e5abe2b62a2e8105ddf2de82c02cd");
  font-weight:normal;
  font-style:normal;
}
@font-face {
  font-family:"Genericons-Neue";
  src:url("fonts/Genericons-Neue.eot?3d5e5abe2b62a2e8105ddf2de82c02cd");
  src:url("fonts/Genericons-Neue.eot?#iefix") format("embedded-opentype"),
  url("data:application/x-font-woff;charset=utf-8;base64,") format("woff"),
  url("fonts/Genericons-Neue.ttf?3d5e5abe2b62a2e8105ddf2de82c02cd") format("truetype");
  font-weight:normal;
  font-style:normal;
}

html {
  font-size: 10px;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  height: 100%;
  background: linear-gradient(rgba(155, 155, 155, 0.1), rgba(155, 155, 155, 0.4)), url('/src/images/europa_final.jpg');
  background-size: cover;
  background-attachment: fixed;
}

body {
  //background: linear-gradient(rgba(252, 252, 252, 0.9), rgba(255, 255, 255, 0.9));
  font-size: 12px;
  height: 100%;
  margin: 0;

}

.fade {
  z-index: 999;
  background: rgba(0,0,0,.01);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

a {
  text-decoration: none;
  color: forestgreen;
}